                })
              }
            }
          }
        }
      }
      console.log('monthData', monthData)
      for (let key in monthData) {
        let lineProcessData = {
          label: key,
          borderColor: '#' + vm.intToRGB(vm.hashCode(key)),
          backgroundColor: 'transparent',
          data: []
