        }
        let lineProcessData = {
          label: key,
          borderColor: '#' + vm.intToRGB(vm.hashCode(key)),
          backgroundColor: 'transparent',
          data: labelsCustomMonth[key]
        }
        datasetsCustom.push(lineProcessData)
      }
      if (currentQuerys.hasOwnProperty('govAgencyCode') && currentQuerys['govAgencyCode'] !== undefined && currentQuerys['govAgencyCode'] !== '') {
        delete datasetsCustom[0]
      }
     let colorDK = []
     let seriesChartBarData = []
     for (let key in datasetsCustom) {
       console.log('datasetsCustom[key]', datasetsCustom[key])
        seriesChartBarData.push(datasetsCustom[key]['data'])
        colorDK.push(datasetsCustom[key]['borderColor'])
      }
      console.log('seriesChartBarData', seriesChartBarData)
      vm.seriesChartBar = []
      vm.seriesChartBar = [{
        data: seriesChartBarData
      }]
      vm.reloadBar = false
      vm.chartOptionsBar = {}
      vm.chartOptionsBar = {
        plotOptions: {
          bar: {
            barHeight: '100%',
            distributed: true,
            horizontal: true,
            dataLabels: {
              position: 'bottom'
            }
          }
        },
        colors: colorDK,
        dataLabels: {
          enabled: true,
          textAnchor: 'start',
          style: {
            colors: ['#fff']
          },
          formatter: function(val, opt) {
            if (String(val) > 0) {
              return opt.w.globals.labels[opt.dataPointIndex] + ":  " + val
            } else {
              return ''
            }
          },
          offsetX: 0,
          dropShadow: {
            enabled: true
          }
        },
        stroke: {
          width: 1,
          colors: ['#fff']
        },
        grid: {
          borderColor: '#e7e7e7',
          row: {
            colors: ['#f3f3f3', 'transparent'],
            opacity: 0.5
          }
        },
        xaxis: {
          categories: vm.labelOfLine,
        },
        yaxis: {
          labels: {
            show: false
          }
        },
        tooltip: {
          theme: 'dark',
          x: {
            show: false
          },
          y: {
            title: {
              formatter: function() {
                return ''
              }
            }
          }
        }
      }
      // report 3
      vm.seriesChartBarTotal = []
      vm.seriesChartBarTotal = [{
        name: 'ang x l cn hn',
        data: undueCountData
      },{
        name: 'ang x l qu hn',
        data: overdueCountData
      },{
        name: 'ang b sung iu kin',
        data: waitingCountData
      },{
        name: ' gii quyt sm hn',
        data: betimesCountData
      },{
        name: ' gii quyt ng hn',
        data: ontimeCountData
      },{
        name: ' gii quyt qu hn',
        data: overtimeCountData
      }]
      vm.chartOptionsBarTotal = {}
      vm.chartOptionsBarTotal = {
        plotOptions: {
          bar: {
            barHeight: '100%',
            horizontal: true,
            dataLabels: {
              hideOverflowingLabels: true
            }
          },
        },
        chart: {
          stacked: true,
          locales: [{
            "name": "en",
            "options": {
              "toolbar": {
                  "exportToSVG": "Ti xung SVG",
                  "exportToPNG": "Ti xung PNG"
              }
            }
          }]
        },
        colors: ['#008FFB', '#FF4560', '#FEB019', '#00E396', '#775DD0', '#546E7A'],
        stroke: {
          width: 1,
          colors: ['#fff']
        },
        xaxis: {
          categories: vm.labelOfLine,
          labels: {
            formatter: function(val) {
              return val
            }
          }
        },
        yaxis: {
          title: {
            text: undefined
          }
        },
        dataLabels: {
          enabled: true,
          formatter: function(val) {
            if (Number(val) > 2) {return val}
            return ''
          },
          style: {
            fontSize: '11px',
            fontFamily: 'Roboto, Helvetica, Arial, sans-serif',
            colors: ['white']
          }
        },
        fill: {
          opacity: 1
        },
        tooltip: {
          y: {
            formatter: function (val) {
              return val
            }
          }
        }
      }
    },
    hashCode (str) {
      var hash = 0
      for (var i = 0; i < str.length; i++) {
        hash = str.charCodeAt(i) + ((hash << 5) - hash)
      }
      return hash
    },
    intToRGB (i) {
      var c = (i & 0x00FFFFFF).toString(16).toUpperCase()
      return '00000'.substring(0, 6 - c.length) + c
    }
  }
}
</script>
